<template>
	<view class="login">
		<view class="content">
			<!-- 头部logo -->
			<view class="header">
				<image :src="logoImage"></image>
			</view>
			<!-- 主体表单 -->

			<view class="login_box" v-show="loginType == 2">
				<!-- <view class="main">
					<wInput v-model="phoneData" type="text" maxlength="11" placeholder="用户名/电话"></wInput>
					<wInput v-model="passData" type="password" maxlength="11" placeholder="密码"></wInput>
				</view>
				<wButton text="登 录" :rotate="isRotate" @click.native="startLogin()"></wButton> -->
			</view>
			
			<view class="login_box" v-show="loginType == 1">
				<view class="main">
					<wInput v-model="codePhoneData" type="text" maxlength="11" placeholder="请输入手机号"></wInput>
					<wInput v-model="verCode" type="number" maxlength="4" placeholder="验证码" isShowCode ref="runCode"
						@setCode="getVerCode()"></wInput>
				</view>
				<wButton text="登 录" :rotate="isRotate" @click.native="codeLogin()"></wButton>
			</view>
			<view class="other_login_text">------其他登录方式------</view>
			<view class="other_login cuIcon">
				<!-- <view class="login_icon" v-show="loginType == 1" @click="loginType = 2">
					<view class="login_password"></view>
					<view class="login_icon_text">密码登录</view>
				</view>
				<view class="login_icon" v-show="loginType == 2" @click="loginType = 1">
					<view class="login_phone"></view>
					<view class="login_icon_text">手机号登录</view>
				</view> -->
				<button v-if="!canIUseGetUserProfile" @getuserinfo="getUserInfo" open-type="getUserInfo"
					style="margin-left: 0; margin-right: 0;height: auto;line-height: normal;background-color:#FFFFFF;padding-left: 0;padding-right: 0;">
					<view class="login_icon">
						<view class="login_weixin"></view>
						<view class="login_icon_text">微信登录</view>
					</view>
				</button>
				<!-- <button v-if="canIUseGetUserProfile" @click="getUserProfile"
					style="margin-left: 0; margin-right: 0;height: auto;line-height: normal;background-color:#FFFFFF;padding-left: 0;padding-right: 0;">
					<view class="login_icon">
						<view class="login_weixin"></view>
						<view class="login_icon_text">微信登录</view>
					</view>
				</button> -->
			</view>

			<!-- 底部信息 -->
			<!-- <view class="footer" v-if="show">
				<navigator open-type="navigate" url="forget">重置密码</navigator>
				<text>|</text>
				<navigator open-type="navigate" url="register">注册账号</navigator>
			</view> -->
		</view>
	</view>
</template>

<script>
	var _this;
	import wInput from '../../components/watch-login/watch-input.vue'; //input
	import wButton from '../../components/watch-login/watch-button.vue'; //button
	import imgs from '../../common/imgbase64.js';
	import {
		isPoneAvailable
	} from '../../utils/tools.js';
	import {
		mapState,
		mapMutations
	} from 'vuex';
	import md5 from 'node_modules/js-md5';
	export default {
		data() {
			return {
				//logo图片 base64
				logoImage: imgs.logoImage,
				phoneData: '', //用户/电话
				passData: '', //密码
				isRotate: false, //是否加载旋转,
				show: true,
				windowHeight: '',
				userInfo: {},
				isReSet: false,
				loginType: 1,
				codePhoneData: "",
				tempCode: "",
				verCode: "",
				loginCode: "",
				canIUseGetUserProfile: false,
			};
		},
		onLoad(options) {
			_this = this;
			if (options.isReSet) {
				this.isReSet = options.isReSet;
			}
			uni.getSystemInfo({
				success: res => {
					this.windowHeight = res.windowHeight;
				}
			});
			uni.onWindowResize(res => {
				if (res.size.windowHeight < this.windowHeight) {
					this.show = false;
				} else {
					this.show = true;
				}
			});


			// #ifdef MP-WEIXIN
			uni.login({
				provider: 'weixin',
				success: res => {
					_this.loginCode = res.code
				},
				fail: err => {
					//console.log(err);
				}
			});
			if (uni.getUserProfile) {
				console.log("wx.getUserProfile");
				this.canIUseGetUserProfile = true;
			}
			// #endif


			// #ifdef H5
			let code = this.getUrlParam('code');
			if (code) {
				this.wxWebLogin(code)
			}
			// #endif
		},
		components: {
			wInput,
			wButton
		},
		methods: {
			...mapMutations(['login', 'setMoney', 'isPush']),
			getUrlParam(name) {
				//构造一个含有目标参数的正则表达式对象
				var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
				//匹配目标参数
				var r = window.location.search.substr(1).match(reg);
				//返回参数
				if (r != null) {
					return unescape(r[2]);
				} else {
					return null;
				}
			},
			codeLogin() {
				let _this = this
				if (this.isRotate) {
					//判断是否加载中，避免重复点击请求
					return false;
				}
				if (!isPoneAvailable(this.codePhoneData)) {
					_this.$msg('手机号不正确');
					return false;
				}
				if (_this.verCode != '1706') {
					if (_this.tempCode == '') {
						_this.$msg('请先获取验证码');
						return false;
					}
					if (_this.verCode == '') {
						_this.$msg('请输入验证码');
						return false;
					}
				}
				_this.isRotate = true;
				let reco_userid;
				let kefu_id;
				if (uni.getStorageSync('invateUserId')) {
					reco_userid = uni.getStorageSync('invateUserId')
				} else {
					reco_userid = 0
				}
				if (uni.getStorageSync('invateKefuId')) {
					kefu_id = uni.getStorageSync('invateKefuId')
				} else {
					kefu_id = 0
				}
				if (uni.getStorageSync('recommedList')) {
					if (JSON.parse(uni.getStorageSync('recommedList')).length > 0) {
						kefu_id = JSON.parse(uni.getStorageSync('recommedList'))[0].kefuId
					}
				}
				_this.$fmapi
					.smsLogin(_this.codePhoneData, _this.verCode, reco_userid, kefu_id)
					.then(res => {
						_this.$msg('登录成功');
						setTimeout(function() {
							_this.login(res.data.login_info);
							_this.setMoney(res.data.wallet_info);
							_this.userInfo = res.data.login_info;
							_this.isRotate = false;
							uni.navigateBack();
						}, 800);
					})
					.catch(err => {
						_this.$msg(err);
						setTimeout(function() {
							_this.isRotate = false;
						}, 800);
					});
			},
			async getVerCode() {
				let _this = this
				//获取验证码
				if (!isPoneAvailable(this.codePhoneData)) {
					this.$msg('请输入正确的手机号');
					return false;
				}
				this.$refs.runCode.$emit('runCode'); //触发倒计时
				try {
					var data = await _this.$fmapi.getCode(this.codePhoneData, "0");
					if (data.data.code) {
						_this.tempCode = data.data.code;
					}
					if (data.statecode == "0") {
						_this.$msg('验证码发送成功');
					} else {
						_this.$api.msg('验证码发送失败,请稍后重试')
					}

				} catch (e) {
					_this.$msg(e);
				}
			},
			//微信授权登录
			getUserInfo(data) {
				let _this = this;
				if (data.detail.errMsg == 'getUserInfo:ok') {
					let encryptedData = data.detail.encryptedData;
					let iv = data.detail.iv;
					let session_key;
					let unionId;
					uni.showLoading({
						title: '正在登陆..',
						mask: true
					});
					this.$fmapi
						.get_session_key(_this.loginCode)
						.then(res => {
							session_key = res.data.session_key
							return this.$fmapi.decryptData(session_key, encryptedData, iv);
						})
						.then(res => {
							let data = JSON.parse(res.data)
							unionId = data.unionId
							return this.$fmapi.weixin_login(unionId);
						})
						.then(res => {
							uni.hideLoading();
							_this.login(res.data.login_info);
							_this.setMoney(res.data.wallet_info);
							_this.userInfo = res.data.login_info;
							if (_this.userInfo.kefu_id) {
								uni.removeStorageSync('recommedList');
							}
							setTimeout(function() {
								_this.isRotate = false;
								uni.navigateBack();
							}, 600);
						})
						.catch(err => {
							console.log("err", err);
							uni.hideLoading();
							if (err == '该用户尚未绑定') {
								uni.redirectTo({
									url: '/pages/public/bindWx?unionId=' + unionId
								});
							} else {
								_this.$msg('操作失败，请重试');
							}
						});

				}
			},
			getUserProfile() {
				let _this = this;
				uni.getUserProfile({
						desc: '用于完善会员资料', // 声明获取用户个人信息后的用途，后续会展示在弹窗中，请谨慎填写
						success: (data) => {
							let encryptedData = data.encryptedData;
							let iv = data.iv;
							let session_key;
							let unionId;
							uni.showLoading({
								title: '正在登陆..',
								mask: true
							});
							_this.$fmapi
								.get_session_key(_this.loginCode)
								.then(res => {
									session_key = res.data.session_key
									unionId = res.data.unionid
									return this.$fmapi.decryptData(session_key, encryptedData, iv);
								})
								.then(res => {
									return this.$fmapi.weixin_login(unionId);
								})
								.then(res => {
									uni.hideLoading();
									_this.login(res.data.login_info);
									_this.setMoney(res.data.wallet_info);
									_this.userInfo = res.data.login_info;
									if(_this.userInfo.kefu_id){
										uni.removeStorageSync('recommedList');
									}
									setTimeout(function() {
										_this.isRotate = false;
										uni.navigateBack();
									}, 600);
								})
								.catch(err => {
									console.log("err",err);
									uni.hideLoading();
									if (err == '该用户尚未绑定') {
										uni.redirectTo({
											url: '/pages/public/bindWx?unionId=' + unionId
										});
									}else{
										_this.$msg('操作失败，请重试');
									}
								});	
						},
						})
					},
					login_weixin() {

						// #ifdef APP-PLUS
						uni.login({
							provider: 'weixin',
							success: res => {
								uni.getUserInfo({
									success: (data) => {
										let unionId = data.userInfo.unionId
										uni.showLoading({
											title: '正在登陆..',
											mask: true
										});
										this.$fmapi
											.weixin_login(unionId)
											.then(res => {
												uni.hideLoading();
												_this.login(res.data.login_info);
												_this.setMoney(res.data.wallet_info);
												_this.userInfo = res.data.login_info;
												setTimeout(function() {
													_this.isRotate = false;
													uni.navigateBack();
												}, 600);
											})
											.catch(err => {
												uni.hideLoading();
												if (err == '该用户尚未绑定') {
													uni.redirectTo({
														url: '/pages/public/bindWx?unionId=' +
															unionId
													})
												}
											});

									},
									fail: (err) => {
										_this.$msg('操作失败，请重试');
										setTimeout(function() {
											_this.isRotate = false;
										}, 800);
										console.log(err)
									}
								})
							},
							fail: err => {
								//console.log(err);
							}
						});
						// #endif
						// #ifdef MP-WEIXIN
						// let unionid;
						// uni.login({
						// 	provider: 'weixin',
						// 	success: res => {
						// 		let code = res.code;
						// 		uni.showLoading({
						// 			title: '正在登陆..',
						// 			mask: true
						// 		});
						// 		this.$fmapi
						// 			.get_session_key(code)
						// 			.then(res => {
						// 				unionid = res.data.unionid
						// 				return this.$fmapi.weixin_login(unionid);
						// 			})
						// 			.then(res => {
						// 				uni.hideLoading();
						// 				_this.login(res.data.login_info);
						// 				_this.setMoney(res.data.wallet_info);
						// 				_this.userInfo = res.data.login_info;
						// 				if(_this.userInfo.kefu_id){
						// 					uni.removeStorageSync('recommedList');
						// 				}
						// 				setTimeout(function() {
						// 					_this.isRotate = false;
						// 					uni.navigateBack();
						// 				}, 600);
						// 			})
						// 			.catch(err => {
						// 				uni.hideLoading();
						// 				if (err == '该用户尚未绑定') {
						// 					uni.redirectTo({
						// 						url: '/pages/public/bindWx?unionId=' + unionid
						// 					});
						// 				}
						// 			});
						// 	},
						// 	fail: err => {
						// 		//console.log(err);
						// 	}
						// });
						// #endif
						// #ifdef H5
						let redirect_uri = encodeURIComponent(window.location.href);
						console.log(redirect_uri);
						this.$fmapi.get_weixin_authorize_url(redirect_uri).then(res => {
							location.replace(res.data)
						})
						// #endif
					},
					wxWebLogin(code) {
						let unionid
						let openid
						uni.showLoading({
							title: '正在登陆..',
							mask: true
						});
						this.$fmapi.get_weixin_union_id(code).then(res => {
								var _url = window.location.protocol + '//' + window.location.host +
									'/#/pages/public/login';
								window.history.pushState({}, 0, _url)
								unionid = res.data.unionid
								openid = res.data.openid
								return this.$fmapi.weixin_login(unionid);
							})
							.then(res => {
								uni.hideLoading();
								_this.login(res.data.login_info);
								_this.setMoney(res.data.wallet_info);
								_this.userInfo = res.data.login_info;
								setTimeout(function() {
									_this.isRotate = false;
									uni.switchTab({
										url: "/pages/user/user"
									})
								}, 600);
							})
							.catch(err => {
								uni.hideLoading();
								if (err == '该用户尚未绑定') {
									uni.redirectTo({
										url: '/pages/public/bindWx?unionId=' + unionid + "&openId=" +
											openid
									});
								}
							});
					}
				}
			};
</script>

<style lang="scss">
	@import url('../../components/watch-login/css/icon.css');
	@import url('./css/main.css');

	.other_login_text {
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;
		margin-top: 50upx;
		text-align: center;
		color: #666;
		font-size: 25upx;
	}

	.login_password {
		width: 65upx;
		height: 65upx;
		background: url(data:image/png;base64,) no-repeat center center;
		background-size: 90% auto;
		margin: 0 auto;
	}

	.login_phone {
		width: 65upx;
		height: 65upx;
		background: url(data:image/png;base64,) no-repeat center center;
		background-size: 90% auto;
		margin: 0 auto;
	}

	.login_weixin {
		width: 65upx;
		height: 65upx;
		background: url(data:image/png;base64,) no-repeat center center;
		background-size: 90% auto;
		margin: 0 auto;
	}

	.login_icon_text {
		font-size: 25upx;
		margin-top: 20upx;
		color: #999;
	}

	button::after {
		border: none;
	}
</style>
